<template>
    
      <table class="edit-table"  v-show="flag" :ref="header">
          <thead> 
  <tr  class="tr-head">
            <th class="td-buttons">                <AddTool @add-item="addItem" @reload-items="reloadItems" :tool="table[0]" :type="type" :hamal="hamal" :key="tabKey" />
</th>
                <th>  </th>
                  <th> ip </th>
               <th  v-for="info in Object.keys(table[0].details.info)" :key="info" >
                        {{info}}
                      </th>
                  <th v-for="boolChecks in Object.keys(table[0].details.boolChecks)" :key="boolChecks" >
                        {{boolChecks}}
                      </th>
                      <th>  </th>
                                            <th>  </th>
            </tr>
        
          </thead>
           <tbody>
          
       
            <!-- <tr v-if="isEditMode">edit modeeeeeeee</tr> -->
            <!-- <tr v-for="tool in table " :key="tool" v-if="!isEditMode"> -->

    <tr v-for="(tool,index) in table " :key="tool">

            
            <td v-show="tool.id != editModeId" class="td-buttons">
                <DeleteEditBtns @delete-item="deleteItem(tool.id)" @reload-items="reloadItems" @start-edit-mode="startEditMode(tool.id,tool)" :title="tool.Title" :id="tool.id"  :isLast="table.length"/>
            </td>
          
                <td v-if="tool.id != editModeId" >
                    
                    {{tool.Title}}  </td>
               
                <td v-if="tool.id != editModeId" > {{tool.ip}}</td>

                  <td  v-if="tool.id != editModeId"  v-for="(info,index) in Object.keys(tool.details.info)" :key="index" class="checkbox-bug"> 
                        {{tool.details.info[info]}}
                      </td>
             
                  <td v-if="tool.id != editModeId"  v-for="(boolCheck,index) in Object.keys(tool.details.boolChecks)" :key="index" class="checkbox-bug"> 
                      <input disabled="disabled" type="checkbox" :checked="!tool.details.boolChecks[boolCheck]" :style="!tool.details.boolChecks[boolCheck] ? Xstyle : Ystyle"  @change="updateReport($event,tool.details.boolChecks,boolCheck,tool)" > 
                   
                      </td>
                      <td v-if="tool.id != editModeId" >
                         
                          <q-select  class="sizing" :value="tool.delayCause" dark color="white"  @update:model-value="(value)=>{updateDelayCause(value,tool.Title)}"  v-model="tool.delayCause" v-if="Object.keys(tool.details.boolChecks).some((el)=>{
                          return !tool.details.boolChecks[el]
                          })" >
                          </q-select>
                     
                          </td>  
                          <td v-if="tool.id != editModeId"  class="comments"> 
                              <textarea disabled="disabled" max-rows="3" @input="updateLocal(tool.Title,tool.comments)" placeHolder="       " maxlength="45" class="comments-input" v-model="tool.comments">
                              </textarea>
                          </td>
                <EditToolMode v-if="tool.id == editModeId" :type="type" :tool="tool" @reupdate-tool="reupdateTool" @reload-items="reloadWithParam" :tdWidth="tdWidth" :isFirst="!index" @end-edit-mode="endEditMode()" />
                </tr>
                <!-- <AddTool @add-item="addItem" @reload-items="reloadItems" :tool="table[0]" :type="type" :hamal="hamal" :key="tabKey"/> -->
                <!-- <AddTool :tool="table[0]" :type="type" :hamal="hamal"/> -->
          </tbody>
              </table>
</template>
<script>
import AddTool from "./AddTool"
import DeleteEditBtns from "./DeleteEditBtns"
import EditToolMode from "./EditToolMode.vue"

export default {
    props:["newTable","type","hamal"],
    name:"TableSlide",
    data(){
        return{
            reasonsArray:["soc","pc",'"',"VIP",' "',"","VC"," ",""],
            flag:false,
            deme:'',
            header:"",
            table:{},
            labels:{},
            model:["soc"],
            tdWidth:'',
            label:"",
            isInfo:false,
            boolChecks:[],
        
            Ystyle:{
                "--clip-path":'polygon(14% 44%,0 65%,50% 100%,100% 16%, 80% 0, 43% 62%)',
                "--shadow-color":'rgb(104, 243, 76)'

            },
            Xstyle:{
                "--clip-path":'polygon(20% 0%,0% 20%, 30% 50%,0% 80%,20% 100%, 50% 70%, 80% 100%, 100% 80%, 70% 50%,100% 20%, 80% 0%, 50% 30%)',
                "--shadow-color":'rgb(236, 71, 71)'

            },
            submitArr:[],
            tabKey : 0,
            // isEditMode:false,
            editModeId : -1,
            reupdateToolData : {},
        }
    },
    components:{
        AddTool,
        DeleteEditBtns,
        EditToolMode
    },

    methods:{
         asyncParse(jsonTools){
            //empty!!!!!!

            return new Promise((resolve,reject)=>{
                resolve(JSON.parse(jsonTools))
            })
         },
     async   reupdateTool(oldTool){
            this.reupdateToolData = await this.asyncParse((this.reupdateToolData))
  


            for (let i = 0; i < this.table.length; i++) {
         

         
                if (this.reupdateToolData.id == this.table[i].id){
         
                    this.table[i] = this.reupdateToolData
                }
                
            }
            //not working, update newTool in table
        },
        deleteItem(id){
            const index = this.table.findIndex((obj)=>obj.id ==id)
            this.table.splice(index,1)
            this.$swal.hideLoading()
                this.$swal.close()
                    this.$emit("reload-items",false)

        },
         addItem(item){
  
            this.table.push(item)
            this.$swal.hideLoading()
            this.$swal.close()
            console.log("here")

                        this.$emit("reload-items",false)
        },
        reloadWithParam(isFirst){
           console.log(isFirst)
            this.tabKey +=1; 
            if(isFirst){
              this.$emit("reload-items",isFirst)

            }
   
        },
        reloadItems(isFirst){
            console.log(isFirst)
            this.tabKey +=1; 
            if(isFirst){
              this.$emit("reload-items",isFirst)

            }
   
            
        },
        startEditMode(id,tool){

            // this.isEditMode = true;
            this.editModeId = id;

            this.reupdateToolData = JSON.stringify(tool)
            // console.log(this.reupdateToolData)

            // this.reupdateToolData= Object.assign(tool);
            // console.log('this.reupdateToolData1')
            // console.log(this.reupdateToolData)
        },
        endEditMode(){
            this.editModeId = -1;
        },
        updateLocal(Title,comment){


                             this.$emit("on-comment",Title,comment)

        },
        setter(elementType,length){

            var calc = `${97/length}`;
            calc +='vw'

               var elements  =  this.$refs[this.header].getElementsByTagName(elementType);
            [...elements].forEach((element)=>{
                element.style.setProperty("--width-var",calc)
            })

        },
        setWidthTables(length){
            this.setter("td",length)
            this.setter("th",length)
        
        

        },
        promiseParse(){
            var vm  =this;
        return new Promise((resolve,reject)=>{
               for(var i = 0 ;i<vm.table.length;i++){
                vm.table[i]["details"] = JSON.parse( vm.table[i]["details"]) 
            }
        
                vm.isInfo = vm.table[0].details.info ? true: false      

            resolve(vm.table)
        })
    },

    
        updateDelayCause(delayCause,title){
            this.submitArr.forEach((item)=>{
               if(item.Title ==title ){
                   item.delayCause = delayCause;
               }
           })
             this.$emit("on-delay",delayCause,title)

        },
        changeLabelAndUpdateData(tool,title,reason){
          
           tool["delayCause"] =reason;
           this.submitArr.forEach((item)=>{
               if(item.Title == title){
                   item.delayCause = reason;
               }
           })
        },
       
        
     
      
        setCheckedIcons(element){
            if(!element.checked){
                element.style.setProperty("--clip-path",'polygon(14% 44%,0 65%,50% 100%,100% 16%, 80% 0, 43% 62%)')
                element.style.setProperty("--shadow-color",'rgb(104, 243, 76)')
            }
            else{
                element.style.setProperty("--clip-path",'polygon(20% 0%,0% 20%, 30% 50%,0% 80%,20% 100%, 50% 70%, 80% 100%, 100% 80%, 70% 50%,100% 20%, 80% 0%, 50% 30%)')
                element.style.setProperty("--shadow-color",'rgb(236, 71, 71)')
            }
        },
        openModal(Title){
            var toolIndex = 0;
            var vm = this;
        
            this.submitArr.forEach((item,index)=>{
                if(item.Title == Title){
                    toolIndex = index;

                    vm.$swal({
                        title:"" + item.Title,
                        input:"textarea",
                        direction:"rtl",
                        inputPlaceholder:" ",
                        inputClass:"direction-class",
                        confirmButtonText:'',
                        confirmButtonColor:"#7393b3"}).then((response)=>{
                        var finalComment = response.value;
            }) 
                }  
            })

        },

        updateReport(event,arr,boolCheck,tool){
            arr[boolCheck]=!arr[boolCheck]
            this.setCheckedIcons(event.target)

                if(!this.checkIfTakul(arr)){
                this.submitArr.forEach((item)=>{
                    if(item.Title == tool.Title){
                        item.delayCause ="";
                    }
                    })
                    tool.delayCause = ""
                }
            
            var sendArr =  this.submitArr;
                this.$emit("on-failure",sendArr)
            },
            checkIfTakul(object){
                if(Object.keys(object).some((element)=>{
                    return !object[element]
                })){
                    return true;
                
                }else{
                    return false; 
                }
            },
        
      
        createSubmitArr(){
            for( var i =0 ; i <this.table.length;i++){
                var currentTool = this.table[i];
                if(this.isInfo){
                    this.submitArr.push({ip:currentTool.ip,comments:currentTool.comments,delayCause:currentTool.delayCause,Title:this.table[i].Title,type:currentTool.type,details:{info:currentTool.details.info,boolChecks:currentTool.details.boolChecks}})

                }else{
                        this.submitArr.push({ip:currentTool.ip,comments:currentTool.comments,delayCause:currentTool.delayCause,Title:this.table[i].Title,type:currentTool.type,details:{boolChecks:currentTool.details.boolChecks}})

                }
            }
        }
    },
   async  beforeMount(){
          
             this.table = this.newTable
                if(!sessionStorage.getItem(this.type)){
                    sessionStorage.setItem(this.type+'deme',JSON.stringify(this.table[0]["details"]))
                    sessionStorage.setItem(this.type,Object.keys(this.table[0]["details"]["boolChecks"]).length+Object.keys(this.table[0]["details"]["info"]).length+5)
            }
            this.deme = await this.asyncParse(sessionStorage.getItem(this.type+'deme'))
        
            //     if( typeof this.table[0]["details"] == 'string' ){
            // this.table = await this.promiseParse()
            //     }
        // for(var i =0 ;i<this.table.length;i++){
        //     this.table[i]["details"]["info"]= {}

        // }
      
    //   console.log('table:')
    //   console.log(this.table)
        // this.boolChecks = [...Object.keys(this.table[0].details.boolChecks)]
        this.createSubmitArr()

        if(this.type ==''){
            this.header = ''
        }if(this.type ==' '){
            this.header =' '
        }if(this.type ==''){
            this.header =''
        }if(this.type == 'vc'){
            this.header = " vc"
        }
        this.submitArr.forEach((item)=>{
            if(!this.checkIfTakul(item.details.boolChecks)){
                item.delayCause = "";

            }
        })
          this.table.forEach((item)=>{
            if(!this.checkIfTakul(item.details.boolChecks)){
                item.delayCause = "";

            }
        })
        
    },
    
    mounted(){ 
               this.flag = false;
        this.tdWidth = sessionStorage.getItem(this.type)
        this.setWidthTables(this.tdWidth)
               this.flag = true;

    
  
        
    }
}
</script>

<style scoped>
.plus-btn{
    color:white;
    border-color:transparent;
    background-color:var( --primary-clr);
    font-size: 1.3rem;
    padding:0.2em 1rem;
    border-radius: 50%;
}
.comments-input{
        text-align: center;
        resize: none;
        background-color: transparent;
        color:var(--primary-color);
        width:90%;
        /* height: 70px; */
        border:0;
        font-size:18px;
        
        padding-right:0px !important;
        position: relative;
        margin:0 auto;
        outline:0;
        
}
/* .tool-title-container{  
      overflow-wrap: break-word;

    display:inline-block;
    width:var(--width-var) !important;

} */
.table>:not(caption)>*>*{
    padding:0;
    border-bottom-width: 1px;
    /* padding:12px; */
}
.checkbox-bug{
    position:relative;
}
.al{
}
.sizing input{
    font-size:1.2vw;
    position: relative;
    top: 30px;
}
.popup-select-sizing{
    text-align: center;
}
.po{
    position:absolute;
    top:50%;
    left:50%;
    /* transform: translate(-50%,-50%); */
    height:100%;
    width:100%;
}
.container-of-dropdown{
    position: relative;
    border-collapse: collapse !important;
    padding:0;
    font-size:0.8vw;
 
}
.di{
    direction:rtl;
    height:80% !important;
    width:80% !important;
}

.con{
    width: 100%;
    height: 100%;
}
.comments{
    font-size:var(--table-font);
}

.direction-class{
    direction: rtl !important;
}
td,th{
    width:var(--width-var);
    word-wrap: break-word;
    text-align: middle;
    border-bottom:1px white;
    vertical-align: middle;
}
th{
    padding-top:0.5em !important    ;
    padding-bottom: 0.5em !important;
}
input[type="checkbox"]{
    
    appearance: none;
    background-color: transparent;
    margin:0;
    display: grid; 
    outline: 0;
    transform: none !important;
    position:relative;
    margin: 0 auto;
    place-content: center; 
    font-size: 2.10vw !important;
    color:greenyellow;
    width:30px;
    height:30px;
    border-radius: 5px;
}
:disabled{
    cursor:default !important;
}
input[type="checkbox"]:checked::before{
}
input[type="checkbox"]::before{
    width:33px;
    outline: 0;
    border: 0;
    text-decoration: none;
    height:33px;
    box-shadow: inset 1em 1.8em  var(--shadow-color);
    content: "";
    transform-origin:bottom;
    clip-path:var(--clip-path);
}
.slider-container{
    position: absolute;
    top:10%;
    height:80%;
}
h3{
    text-align: center;
    position: relative;
    top:0%;
    color:  var(--primary-color);
    font-size:4vw;
}
.header{
    width: 100%;
}
tbody{
    display:block;
    width:97vw;
    height:100%;
    overflow-y:auto;
    overflow-x:hidden;
}
thead tr{
    display:block;
    width:90vw;
    
}
tbody tr{
    transition:background-color 0.2s ease-in-out;
    /* display:block; */
    height :70px !important;
   /* border-bottom-width: 1px !important */
}
tbody tr:hover{
    background-color: rgba(128, 128, 128, 0.308);
}
.edit-table{
    
    color: var(--primary-color);
    text-align: center;
    border-color:var(--primary-color);
    direction: rtl;
    table-layout: fixed;
    margin-bottom:0;
    width: 97vw !important;
    background-color: rgba(185, 90, 90, 0) !important ;
    font-family:cellfont;
     height:90%; 
    position:relative;
    font-size:var(--table-font) !important;    
}
.tr-head{
   /* background-color: red !important; */
   border-bottom-width:1px;
   font-weight: bold;
   height: unset !important;
    width:97vw;
   display: block;
}
.td-buttons{
    /* background-color: red; */
    /* padding: 0; */
    /* border: 0px; */
    /* text-align: center; */
    /* display: flex; */
    /* justify-content: center; */
    /* align-content: center; */
}
td{
}
</style>