<template>
  
    <table class="table">

          <tbody>
                <tr tabindex="-1" v-for="hamal in filteredArray" :v-ripple="false" :marked="false" @click="markCell(hamal.Title)" :ref="hamal.Title+'tr'" :key="hamal">
                 
                    <td class="hamal-title"   :ref="hamal.Title"> 
                        {{hamal.Title}}
                    </td> 
                    <td class="hamal-lastDateChecked"  > 
                        {{hamal.lastDateChecked}}
                    </td> 
                
                </tr>


          </tbody>
              </table>


</template>

<script>
export default {
    name:"HamalTable",
    props:["filteredArray"],
    methods:{

         markCell(TitleRef){
            //  console.log(this.filteredArray)
            var chosenValue=this.$refs[TitleRef][0].innerHTML;
            this.$refs[TitleRef+'tr'][0].focus()
            this.$emit("change-blue",chosenValue)
      
        
    },
    }

}
</script>

<style scoped>

tbody tr:hover{
    background-color:rgba(235, 227, 227, 0.466);
}

th,td{
    user-select: none
    ;
    padding: 0.5em 1em;
        width:25vw; 
    text-align: center;
    
}


table{
    color:white;
    direction: rtl;
    table-layout: fixed;
    /* color:white; */
    margin-bottom:0;
    background-color: rgba(185, 90, 90, 0) !important ;
    font-family:cellfont;
     height:auto; 
    
    position:absolute;
    top:0%;
    left:50%;
    transform: translate(-50%,0);
    width: 50vw;
    font-size: 1.6rem !important;
    border-bottom: 1px solid grey;
    
}tr:focus{
    background-color: var(--primary-clr) !important;
    border-style:none;
    color:white;
    outline: none;
} tbody tr {
    transition: all 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
    cursor: pointer;
}
.table:last-child {
    /* border-bottom: none; */
}
.table>:not(:last-child) {
    border-bottom: none;
}
tbody{
    display: block;
    width: 100%;
    overflow-x: hidden;
    overflow-y:auto;
    /* height:350px; */
}
thead tr {
    display: block;
    width:45vw;
}
</style>