<template>

    <div class="container-background" ref="background">  
        <div class="connection-flex" v-if="mode=='check'"> 
    <div v-if="isOnline"> 
    <span class="bi bi-wifi internet "> </span>
    <!-- <h4>       </h4>  -->
    </div> 
    <div  v-if="!isOnline" > 
            <span class="bi bi-wifi-off no-internet"> </span>
    <!-- <h4>     </h4>  -->

        </div> 
     </div> 
        <div v-if="mode=='check'" class="name-block" v-show="userName"> 
            <span > : {{userName}} </span>
                        <span >  : {{mahlaka}} </span>

            <span> {{date}}</span>
            <span> {{chosenValue}}</span> 
            </div> 
            
                    <h1  v-if="mode=='edit'" class="main-title">  </h1>

    <h1 v-if="mode=='check'" class="main-title">  </h1>

    <h3>  " </h3>

          <button  v-if="mode=='check'" class="submit-button" ref="submit-button" v-ripple @click="submitHandler" :class="{blued:isChose}">  </button >
                    <button v-if="mode=='edit'" class="submit-button" ref="submit-button" v-ripple @click="submitHandler" :class="{blued:isChose}">  </button >

          <div class="carousel-buttons-container">
            <q-btn-toggle @update:model-value="imageFunc" 
            flat
            size="1.4rem"
            v-model="sliders"
            :options="options"
            />
            </div>
            <table  class="flex-headers">
                <tr>
                <td>
                   "
                </td>
                
                <td >
                    
                    </td>
                    </tr>
                </table>

<q-carousel  ref="qar" @before-transition="imageFuncNew"
        v-model="sliders"
        animated
        class="carousel-check"
         transition-prev="slide-right"
         transition-next="slide-left"
        
        
      swipeable
        
        
         
        color="dark"
        height="30%"
      >
         <q-carousel-slide class="column no-wrap flex-center p0 ani"  v-for="(place) in locations" :name="place.location"  :key="place.location">
             <HamalTable   @change-blue="updateValues" :filteredArray="getArrayByPlace(place.location)"   />
          </q-carousel-slide>

        </q-carousel>



              </div>


</template>

<script>
import HamalTable from "./HamalTable"
import axios from "axios"
import moment from "moment"
export default {
    props:["mode","hamals","mahlaka","userName"],
    name:"HamalForm",
    data(){
        return{
                      isOnline:navigator.onLine,

            isLocalEmpty:true,
            isFormDialog:false,
            filteredArray:[],
            arr:[],
            date:moment(new Date(),'LLLL','he').format('DD/MM  dddd '),
            clickFunc : (event)=>{
            var element = event.target;
            
            if (element.tagName != "TD" && element != this.$refs["submit-button"]){
                this.isChose = false;
                this.chosenValue = ""

            } 
        },
            chosenLocation:"",
            options:[
                {
                    label:' "',
                    value:' "',
                    src:"@/assets/binyan.png"
                    
                },
                {
                    label:" ",
                    value:" ",
                    src:"@/assets/binyan.png"
                },
                {
                    label:"",
                    value:"",
                    src:"@/assets/binyan.png"
                }
            ],
            chosenValue:'',
            isChose:false,
            locations:[
                {
                    location:' "',
                    image:""
                }
                ,{
                    location:" ",
                    image:""

                },{
                location:"",
                image:""
                }
            ],
            sliders:' "',
            chosenRef:"",
            //  urlForHamalsLocal: `${this.$url}/hamals`,
            //  urlForChecksLocal: `${this.$url}/checks`,

            //  urlForToolsLocal: `${this.$url}/tools`,
             newvalUrl : this.$url,
             chosenValueId: 0,


        }
    }   ,
    beforeUnmount(){
        
        window.removeEventListener("click",this.clickFunc)
    },
    components:{
        HamalTable
    }
,    methods:{
        
      
    imageFuncNew(newVal,oldVal){
         this.chosenLocation =newVal;
       this.$refs.qar.goTo(newVal)
        if(newVal == ' '){
             this.$refs.background.classList.remove("marganit")
          this.$refs.background.classList.remove("binyan-zahal")
             this.$refs.background.classList.add("mezudat-zion")

        } if(newVal ==' "'){
                this.$refs.background.classList.remove("mezudat-zion")
                this.$refs.background.classList.remove("marganit")
                this.$refs.background.classList.add("binyan-zahal")
        }

            if(newVal ==''){
                this.$refs.background.classList.remove("binyan-zahal")
                 this.$refs.background.classList.remove("mezudat-zion")
                this.$refs.background.classList.add("marganit")



            }

    },
    imageFunc(value){
       this.chosenLocation =value;
       this.$refs.qar.goTo(value)
        if(value == ' '){
             this.$refs.background.classList.remove("marganit")
          this.$refs.background.classList.remove("binyan-zahal")
             this.$refs.background.classList.add("mezudat-zion")

        } if(value ==' "'){
                this.$refs.background.classList.remove("mezudat-zion")
                this.$refs.background.classList.remove("marganit")
                this.$refs.background.classList.add("binyan-zahal")
        }

            if(value ==''){
                this.$refs.background.classList.remove("binyan-zahal")
                 this.$refs.background.classList.remove("mezudat-zion")
                this.$refs.background.classList.add("marganit")



            }
        
    },
    asyncStringify(toolsArray){
        return new Promise((resolve,reject)=>{
            resolve(JSON.stringify(toolsArray))
        })

    },
    
    updateValues(value){
        console.log(value)
        this.isChose = true;
        this.chosenValue = value;

    }
    ,
     getCheckIdkByCheckSp(value){

            return axios.get(this.newvalUrl+`/_api/web/lists/getByTitle('checks')/items`,{params:{$filter:`Title eq '${value}' and mahlaka eq '${this.mahlaka}'`,$top:1,$select:'checkId'}})
        },
   
   getCheckIdkByCheckDjango(value){
          //newval
            // return axios.get(this.urlForChecksLocal,{params:{"Title":value}})
            return axios.get(this.newvalUrl + '/check/name/' + value +'/mahlaka/'+this.mahlaka+'/',{
                headers:{
                Authorization:`Token ${sessionStorage.getItem("token")}`
                }
            })
        },

    getHamalIdByTitleLocal(checkId){
        //newval
            // return axios.get(this.urlForHamalsLocal,{params:{"checkId":checkId}})
            return axios.get(this.newvalUrl + '/hamal/check/' + checkId +'/')
    },
    getToolsByHamalIdLocal(hamalId){
        //newval
                // return  axios.get(this.urlForToolsLocal,{params:{"hamalId":hamalId}})
                return axios.get(this.newvalUrl + '/tool/hamal/' + hamalId +'/')
    },

//   async getStuffProd(){
      
//        var checkIdResponse = await this.getCheckIdkByCheckProd(this.chosenValue);
//        var checkId =checkIdResponse.data.value[0]["checkId"]
       
//             localStorage.setItem("route-params",JSON.stringify({mahlaka:this.mahlaka,userName:this.userName,location:this.chosenLocation,checkName:this.chosenValue,checkId:checkId}))
//             this.$router.push({path:`/${checkId}`,name:"check",params:{mahlaka:this.mahlaka,userName:this.userName,location:this.chosenLocation,checkName:this.chosenValue,checkId:checkId}})

 
           


        
//     },
  
    async getStuffSp(){
            var checkIdResponse = await this.getCheckIdkByCheckSp(this.chosenValue);
       var checkId =checkIdResponse.data.value[0]["checkId"]
       
            localStorage.setItem("route-params",JSON.stringify({mahlaka:this.mahlaka,userName:this.userName,location:this.chosenLocation,checkName:this.chosenValue,checkId:checkId}))
            this.$router.push({path:`/${checkId}`,name:this.mode,params:{mahlaka:this.mahlaka,userName:this.userName,location:this.chosenLocation,checkName:this.chosenValue,checkId:checkId}})

    },
    async getStuffDjango(){
        
        var checkIdResponse = await this.getCheckIdkByCheckDjango(this.chosenValue)
        var checkId = checkIdResponse.data[0]["id"];
        
            localStorage.setItem("route-params",JSON.stringify({mahlaka:this.mahlaka,userName:this.userName,location:this.chosenLocation,checkName:this.chosenValue,checkId:checkId}))
            this.$router.push({path:`/${checkId}`,name:this.mode,params:{mahlaka:this.mahlaka,userName:this.userName,location:this.chosenLocation,checkName:this.chosenValue,checkId:checkId}})
       

            // localStorage.setItem("route-params",JSON.stringify({mahlaka:this.mahlaka,userName:this.userName,location:this.chosenLocation,checkName:this.chosenValue,checkId:checkId}))
            // this.$router.push({path:"/check",name:"check",params:{mahlaka:this.mahlaka,userName:this.userName,location:this.chosenLocation,hamalsList:promisesArr,checkName:this.chosenValue,checkId:checkId}})
         
    },
 async getStuffDjangoEdit(){
  
 var checkIdResponse = await this.getCheckIdkByCheckDjango(this.chosenValue)
        var checkId = checkIdResponse.data[0]["id"];
        
            localStorage.setItem("route-params",JSON.stringify({mahlaka:this.mahlaka,userName:this.userName,location:this.chosenLocation,checkName:this.chosenValue,checkId:checkId}))
            this.$router.push({path:`/${checkId}`,name:"edit",params:{mahlaka:this.mahlaka,userName:this.userName,location:this.chosenLocation,checkName:this.chosenValue,checkId:checkId}})

        //////////////////
        var hamalsResponse = await this.getHamalIdByTitleLocal(checkId);   

        hamalsResponse = hamalsResponse.data.map((hamal)=>{
            var obj = {}
            obj[hamal["hamalId"]] = hamal["Title"];
          return  obj 
            })

      
        var promisesArr = [...hamalsResponse]
        promisesArr = await  axios.all(promisesArr.map((promise)=>{
            
            return this.getToolsByHamalIdLocal(Object.keys(promise)[0])

        }))
    
        promisesArr = promisesArr.map(res =>res.data)
        promisesArr = promisesArr.map((tool)=>{
            var hamalId = tool[0].hamalId;
            const hamalName  = hamalsResponse.filter(hamal=>Object.keys(hamal)[0] == hamalId)[0][hamalId]
            return {"hamalName":hamalName,tools:[...tool]}
        })
       



      
         
     
         
    },

   
    getArrayByPlace(location){
                this.filteredArray=this.arr; 

        var returnedArr = []
   
          this.filteredArray.forEach((hamal)=>{
              if(hamal.location == location){
                  returnedArr.push(hamal)


              }
          })
        return returnedArr
    },
   async submitHandler(){
           if(!window.navigator.onLine){
            this.noConnectionSwal(this.$swal)
            return;
        }
       
        try{

            const response = await this.sendRandomRequest()


        }catch{
                        this.noConnectionSwal(this.$swal)
                                    return;


        }
                if(this.$isDjango){
                     this.getStuffDjango()

                }else{
                    this.getStuffSp()
                }
            
   

    },
    asyncParse(jsonTools){
        return new Promise((resolve,reject)=>{
            resolve(JSON.parse(jsonTools))
        })

    },
    filteringFunc(){

       var hamalValue =  this.$refs["input"].value.trim()
        this.filteredArray=this.arr; 
        this.filteredArray = this.filteredArray.filter((item)=>{
            return item.Title.trim().includes(hamalValue) || item.location.trim().includes(hamalValue) 
        })
            

    }
},
  async  mounted(){
        this.imageFunc(' "')
        this.arr = this.hamals;

        
        this.isMobile= window.screen.availHeight < 1000 ? true : false;
      
        window.addEventListener("click",this.clickFunc)
    }

}
</script>

