Vue.use(VueSweetAlert2);
    
Vue.config.globalProperties.$isDjango = window.location.href.includes('portal.army.idf') ? false : true


Vue.config.globalProperties.$mahlaka = "vc"
const djangoUrl = process.env.NODE_ENV == 'development' ? 'http://127.0.0.1:8000' : ""
Vue.config.globalProperties.$url = Vue.config.globalProperties.$isDjango ? djangoUrl :"https://portal.army.idf/sites/gdud0383/Team"
Vue.config.globalProperties.sendRandomRequest = ()=>{
    const url = new URL(window.location.origin)
    url.searchParams.set('rand',Math.random().toString(36).substring(2,15))
   return  fetch(url.toString(),{methods:'HEAD'})
}
Vue.config.globalProperties.noConnectionSwal = (swalObject)=>{
    var title = "        "
    var text = "       "

    swalObject.fire({
        title:title,
        text:text,
        timer:2000,
        icon:"error"
    })

}


 
Vue.mount('#app')
