                <td><span class="tool-title-container">  {{tool.Title}} </span></td> 
                <td>{{tool.hamal}} </td>

                <td class="overfl-class"> {{getToolErrors(tool.details.boolChecks)}}</td>
                <td>{{tool.delayCause}} </td>
