<script>
export default {
    name:"CheckOut",
    props:["checkOutList"],
    data(){
        return{
        checkOutFinal:[]
        }
    },
    methods:{
        
        getToolErrors(obj){
            var returnedString= ""
            Object.keys(obj).forEach((item)=>{
                if(!obj[item]){
                    returnedString += item +" ,";
                }
            })
            returnedString = returnedString.slice(0,returnedString.length-1)
            return returnedString
        },
       
    },
    

}
