<template>
<!-- <h3> {{header}}</h3> -->
    
      <table class="table"  v-show="flag" :ref="header">
      <thead>
        
               
              <tr> 
                  <th>  </th>
                  <th> ip </th>
               <th  v-for="info in Object.keys(table[0].details.info)" :key="info" >
                        {{info}}
                      </th>
                  <th v-for="boolChecks in Object.keys(table[0].details.boolChecks)" :key="boolChecks" >
                        {{boolChecks}}
                      </th>
                      <th>  </th>
                                            <th>  </th>



              </tr>
          </thead> 
          <tbody>
            <tr v-for="tool in table" :key="tool">
                
                <td><span class="tool-title-container">{{tool.Title}} </span> </td>
               
                <td> {{tool.ip}}</td>

                  <td v-for="(info,index) in Object.keys(tool.details.info)" :key="index" class="checkbox-bug"> 
                        {{tool.details.info[info]}}
                      </td>
             
                  <td v-for="(boolCheck,index) in Object.keys(tool.details.boolChecks)" :key="index" class="checkbox-bug"> 
                      <input type="checkbox" :checked="!tool.details.boolChecks[boolCheck]" :style="!tool.details.boolChecks[boolCheck] ? Xstyle : Ystyle"  @change="updateReport($event,tool.details.boolChecks,boolCheck,tool)" > 
                   
                      </td>
                      <td >
                         
                          <q-select class="sizing" :value="tool.delayCause" dark color="white"  @update:model-value="(value)=>{updateDelayCause(value,tool.Title)}"  v-model="tool.delayCause"  :options="reasonsArray" v-if="Object.keys(tool.details.boolChecks).some((el)=>{
                          return !tool.details.boolChecks[el]
                          })" >
                          </q-select>
                     
                          </td>  
                          <td class="comments"> 
                            
                              <textarea  max-rows="3" @input="updateLocal(tool.Title,tool.comments)" placeHolder="       " maxlength="45" class="comments-input" v-model="tool.comments">
                              </textarea>
                          </td>
                </tr> 
               


          </tbody>
              </table>
</template>

<script>
export default {
    props:["newTable","type","hamal"],
    name:"TableSlide",
    data(){
        return{
            reasonsArray:["soc","pc",'"',"VIP",' "',"","VC"," ",""],
            flag:false,
          
            header:"",
            table:{},
            labels:{},
            model:["soc"],
            label:"",
            isInfo:false,
            boolChecks:[],
        
            Ystyle:{
                "--clip-path":'polygon(14% 44%,0 65%,50% 100%,100% 16%, 80% 0, 43% 62%)',
                "--shadow-color":'rgb(104, 243, 76)'

            },
        
            Xstyle:{
                "--clip-path":'polygon(20% 0%,0% 20%, 30% 50%,0% 80%,20% 100%, 50% 70%, 80% 100%, 100% 80%, 70% 50%,100% 20%, 80% 0%, 50% 30%)',
                "--shadow-color":'rgb(236, 71, 71)'

            },
            submitArr:[]
        }
    },


    methods:{
        updateLocal(Title,comment){
                             this.$emit("on-comment",Title,comment)

        },
        setter(elementType,length){

            var calc = `${90/length}`;
            calc +='vw'

               var elements  =  this.$refs[this.header].getElementsByTagName(elementType);
            [...elements].forEach((element)=>{
                element.style.setProperty("--width-var",calc)
            })

        },
        setWidthTables(length){
            this.setter("td",length)
           this.setter("th",length)

        

        },
        promiseParse(){
            var vm  =this;
        return new Promise((resolve,reject)=>{
               for(var i = 0 ;i<vm.table.length;i++){
                vm.table[i]["details"] = JSON.parse( vm.table[i]["details"]) 
            }
        
                vm.isInfo = vm.table[0].details.info ? true: false      

            resolve(vm.table)
        })
    },

    
        updateDelayCause(delayCause,title){
            this.submitArr.forEach((item)=>{
               if(item.Title ==title ){
                   item.delayCause = delayCause;
               }
           })
             this.$emit("on-delay",delayCause,title)

        },
        changeLabelAndUpdateData(tool,title,reason){
          
           tool["delayCause"] =reason;
           this.submitArr.forEach((item)=>{
               if(item.Title == title){
                   item.delayCause = reason;
               }
           })
        },
       
        
     
      
        setCheckedIcons(element){
            if(!element.checked){
                element.style.setProperty("--clip-path",'polygon(14% 44%,0 65%,50% 100%,100% 16%, 80% 0, 43% 62%)')
                element.style.setProperty("--shadow-color",'rgb(104, 243, 76)')
            }
            else{
                element.style.setProperty("--clip-path",'polygon(20% 0%,0% 20%, 30% 50%,0% 80%,20% 100%, 50% 70%, 80% 100%, 100% 80%, 70% 50%,100% 20%, 80% 0%, 50% 30%)')
                element.style.setProperty("--shadow-color",'rgb(236, 71, 71)')
            }
        },
        openModal(Title){
            var toolIndex = 0;
            var vm = this;
        
            this.submitArr.forEach((item,index)=>{
                if(item.Title == Title){
                    toolIndex = index;

                    vm.$swal({
                        title:"" + item.Title,
                        input:"textarea",
                        direction:"rtl",
                        inputPlaceholder:" ",
                        inputClass:"direction-class",
                        confirmButtonText:'',
                        confirmButtonColor:"#7393b3"}).then((response)=>{
                        var finalComment = response.value;
            }) 
                }  
            })

        },

        updateReport(event,arr,boolCheck,tool){
            arr[boolCheck]=!arr[boolCheck]
            this.setCheckedIcons(event.target)

                if(!this.checkIfTakul(arr)){
                this.submitArr.forEach((item)=>{
                    if(item.Title == tool.Title){
                        item.delayCause ="";
                    }
                    })
                    tool.delayCause = ""
                }
            
          ;
                this.$emit("on-boolcheck", this.submitArr)
            },
            checkIfTakul(object){
                if(Object.keys(object).some((element)=>{
                    return !object[element]
                })){
                    return true;
                
                }else{
                    return false; 
                }
            },
        
      
        createSubmitArr(){
            for( var i =0 ; i <this.table.length;i++){
                var currentTool = this.table[i];
                if(this.isInfo){
                    this.submitArr.push({ip:currentTool.ip,comments:currentTool.comments,delayCause:currentTool.delayCause,Title:this.table[i].Title,type:currentTool.type,details:{info:currentTool.details.info,boolChecks:currentTool.details.boolChecks}})

                }else{
                        this.submitArr.push({ip:currentTool.ip,comments:currentTool.comments,delayCause:currentTool.delayCause,Title:this.table[i].Title,type:currentTool.type,details:{boolChecks:currentTool.details.boolChecks}})

                }
            }
        }
    },
   async  beforeMount(){
          this.table = [...this.newTable]
                if( typeof this.table[0]["details"] == 'string' ){
            this.table = await this.promiseParse()
    
      }

  
        this.createSubmitArr()

        if(this.type ==''){
            this.header = ''
        }if(this.type ==' '){
            this.header =' '
        }if(this.type ==''){
            this.header =''
        }if(this.type == 'vc'){
            this.header = " vc"
        }
        this.submitArr.forEach((item)=>{
            if(!this.checkIfTakul(item.details.boolChecks)){
                item.delayCause = "";

            }
        })
          this.table.forEach((item)=>{
            if(!this.checkIfTakul(item.details.boolChecks)){
                item.delayCause = "";

            }
        })
        this.flag = true;
        
    },
    
    mounted(){ 
  
        this.setWidthTables(Object.keys(this.table[0]["details"]["boolChecks"]).length+Object.keys(this.table[0]["details"]["info"]).length+4)
       
    
  
        
    }
}
</script>

<style scoped>
.comments-input{
        text-align: center;
        resize: none;
        background-color: transparent;
        color:var(--primary-color);
        width:90%;
        /* height: 70px; */
        border:0;
        font-size:18px;
        
        padding-right:0px !important;
        position: relative;
        margin:0 auto;
        outline:0;
        
}
.tool-title-container{  
      overflow-wrap: break-word;

    display:inline-block;
    width:var(--width-var) !important;

}
.table>:not(caption)>*>*{
    padding:0;
    /* padding:12px; */
}
.checkbox-bug{
    position:relative;
}
.al{
}
.sizing input{
    font-size:1.2vw;
    position: relative;
    top: 30px;
}
.popup-select-sizing{
    text-align: center;
}
.po{
    position:absolute;
    top:50%;
    left:50%;
    /* transform: translate(-50%,-50%); */
    height:100%;
    width:100%;
}
.container-of-dropdown{
    position: relative;
    border-collapse: collapse !important;
    padding:0;
    font-size:0.8vw;
 
}
.di{
    direction:rtl;
    height:80% !important;
    width:80% !important;
}

.con{
    width: 100%;
    height: 100%;
}
.comments{
    font-size:var(--table-font);
}

.direction-class{
    direction: rtl !important;
}
td,th{
    width:var(--width-var);
    
    text-align: middle;
    vertical-align: middle;
}
th{
    padding-top:0.5em !important    ;
    padding-bottom: 0.5em !important;
}
input[type="checkbox"]{
    appearance: none;
    background-color: transparent;
    margin:0;
    display: grid; 
    outline: 0;
    transform: none !important;
    position:relative;
    margin: 0 auto;
    place-content: center; 
    font-size: 2.10vw !important;
    color:greenyellow;
    width:30px;
    height:30px;
    border-radius: 5px;
}
input[type="checkbox"]:checked::before{
}
input[type="checkbox"]::before{
    width:33px;
    outline: 0;
    border: 0;
    text-decoration: none;
    height:33px;
    box-shadow: inset 1em 1.8em  var(--shadow-color);
    content: "";
    transform-origin:bottom;
    clip-path:var(--clip-path);
}
.slider-container{
    position: absolute;
    top:10%;
    height:80%;
}
h3{
    text-align: center;
    position: relative;
    top:0%;
    color:  var(--primary-color);
    font-size:4vw;
}
.header{
    width: 100%;
}
tbody{
    display:block;
    width:90vw;
    height:100%;
    overflow-y:auto;
    overflow-x:hidden;
}
thead tr{
    display:block;
    width:90vw;
}
tbody tr{
    height :70px !important;
}
table{
    color: var(--primary-color);
    text-align: center;
    width:90vw !important;
    border-color:var(--primary-color);
    direction: rtl;
    table-layout: fixed;
    margin-bottom:0;
    background-color: rgba(185, 90, 90, 0) !important ;
    font-family:cellfont;
     height:90%; 
    position:relative;
    font-size:var(--table-font) !important;    
}
</style>