<template>
<div class="pre-input-container binyan-zahal" v-if="!isFinished"> 
   <dialog v-if="mode =='check'"  ref="options-dialog"  v-show=" !isLocalEmpty && !isFormDialog"> 
          <h2>       </h2>
          <span>    : {{userName}} </span>
          <span >  {{date}} </span>
          <span>  {{hamal}} </span>
          <div class="dialog-action-buttons-container"> 
            <button  class="continue-report-button"  @click="routeToCheck">   </button> 
            <button class="new-report-button" @click="toggleInputDialog">    </button>
    </div>
</dialog>
       <h1  v-if="mode=='edit'" class="main-title">  </h1>
    <h1 v-if="mode=='check'" class="main-title">  </h1>

        <div class="spinner-container">
        <div class="spinner-border text-light loading-hamal-spinner"  v-if="isWaitingHamalResponse"> </div>
        </div>
    <div class="report-name-mahlaka-container" v-if="!isWaitingHamalResponse"> 
        
        <h2 v-if="mode =='check'">     </h2>
                <h2 v-if="mode =='edit'">     </h2>

        <span>{{date}} </span>
        <div class="flex-input">
          
        <select  v-model="mahlaka" ref="mahlakot" class="form-select form-select-sm categories-class dropdown-menu-right " size="1" aria-label=".form-select-lg example">
            <option value="VC">VC </option> 
        
            <option :value="hamalsString"> " </option> 
            <option  value="VIP" >VIP </option> 
            <option :value="tashtiotString" > </option> 
            

            </select> 
                        <h3  v-if="mode =='edit'">:</h3>

            <input  v-if="mode =='check'" type="text" :style="{'--shadow-color':'0px 0px 15px 0px rgba(22, 22, 22, 0.192)','--placeholder-color':'grey'}" ref="name" placeholder=" " /> 

  </div>
             <button class="sub-btn" @click="saveName">  </button>

 
</div>
<div class="connection-flex" v-if="mode =='check'" > 
    <div v-if="isOnline"> 
    <span class="bi bi-wifi internet "> </span>
    <!-- <h4>       </h4>  -->
    </div> 
    <div  v-if="!isOnline" > 
            <span class="bi bi-wifi-off no-internet"> </span>
    <!-- <h4>     </h4>  -->

        </div> 
     </div> 
<div class="semel-and-title"> 
    <div class="developers-container">
            
        </div> 
            <img src="@/assets/tzameret.png">

    </div>
    
   </div> 
        
      <HamalForm :mode="mode" v-if="isFinished"  :hamals="hamals"  :userName="userName" :date="date" :mahlaka="mahlaka"/>

</template>


<script> 
import axios from "axios"
import HamalForm from "@/components/HamalForm.vue"
import moment from "moment"


export default {
   name: 'Main',

   data(){

      return{
          isOnline:navigator.onLine,
          $q:"",
          isWaitingHamalResponse:false,
         isLocalEmpty:true,
            userName:"",
            isFormDialog:false,
            date:moment(new Date(),'LLLL','he').format('DD/MM  dddd '),
            tashtiotString:'',
            hamalsString:' "',
            djangoLocalUrl:`${this.$url}`,
            
        urlForChecks: `${this.$url}/`,
                urlForReports: `${this.$url}/`

    ,
    mode:'',
        isFinished :false,
        mahlaka:"VC",
        hamal:"",
        hamals:[],

    }  
   },
   beforeMount(){
    this.mode =  this.$route.params.mode 
   },
    async mounted(){
        this.addConnectionsEventListeners();
        
         var routeParams ;
        var params = localStorage.getItem("route-params");
         if(params){
                this.isLocalEmpty = false;
                routeParams = await this.asyncParse(params)
                this.userName = routeParams["userName"];
                this.hamal = routeParams["checkName"];
                this.$refs["options-dialog"].showModal()
          }else{
                  this.$refs["options-dialog"].close()
                //   this.$refs["input-dialog"].showModal()

                this.isFormDialog = true;
            }
        

     },
   methods:{
       getRandomString(){
        return Math.random().toString(36).substring(2,15)
       },
      
       showStatus(){
            if(navigator.onLine){
                this.isOnline = true;
                    this.$q.notify({message:" ",type:"positive"})

            }else{
                                this.isOnline = false;

                this.$q.notify({message:" ",type:"negative"})

            }
       },
       addConnectionsEventListeners(){
            window.ononline = this.showStatus;
            window.onoffline = this.showStatus;

                
            
       },
      asyncParse(jsonTools){
        return new Promise((resolve,reject)=>{
            resolve(JSON.parse(jsonTools))
        })
    },
    closeDialogWithAnimation(dialog){

                dialog.classList.add("disppear-class")
                    var animationEndFunc = ()=>{
                        dialog.close();
                        dialog.removeEventListener("animationend",animationEndFunc)
                        this.isFormDialog = true;
                    }
                dialog.addEventListener("animationend",animationEndFunc,{once:true})

    },
    
     async saveName(){
        if(!window.navigator.onLine){
            this.noConnectionSwal(this.$swal)
            return;
        }
       
        try{
            const response = await this.sendRandomRequest()

        }catch{
                        this.noConnectionSwal(this.$swal)
                                    return;


        }      if(this.mode == 'check'){
              var stopRedFunc = ()=>{
                  this.$refs["name"].placeholder= ' ';
                  this.$refs["name"].style.setProperty("--placeholder-color","grey")
                  this.$refs["name"].style.setProperty("--shadow-color","0px 0px 15px 0px rgba(22, 22, 22, 0.192)")
                  this.$refs.name.removeEventListener("click",stopRedFunc)
              }
              if(!this.$refs["name"].value.trim()){
                  this.$refs["name"].value = "";
                  this.$refs["name"].placeholder = "    "
                  this.$refs["name"].style.setProperty("--placeholder-color","red");
                  this.$refs["name"].style.setProperty("--shadow-color","0px 0px 15px 0px rgba(243, 3, 3, 0.795)")
                  this.$refs.name.addEventListener("click",stopRedFunc,{once:true})
              }else{
                  this.userName = this.$refs["name"].value;
                              localStorage.clear();

                  this.getHamals()
              }}else{
                  
                  this.getHamals()
              }
              ;
          }
,
   async routeToCheck(){
            const routeParams = await this.asyncParse(localStorage.getItem("route-params"))
            this.$router.push({path:`/${localStorage.getItem("route-params")['checkId']}`,params:{checkId:routeParams['checkId']},name:"check"})

    },
     toggleInputDialog(){
         localStorage.clear();
                              this.closeDialogWithAnimation(this.$refs["options-dialog"])


    },
    
   async getHamals(){
    let response;
    let reports
    let reportsSort = {}
    this.isWaitingHamalResponse = true;
      if(this.$isDjango){
            response = await axios.get(this.urlForChecks+`check/mahlaka/${this.mahlaka}/`,
            {headers:{
                Authorization:`Token ${sessionStorage.getItem("token")}`
            }}
            );
            this.hamals =  response.data;
            reports = await axios.get(this.urlForReports+`report/`,{
                headers:{
                Authorization:`Token ${sessionStorage.getItem("token")}`
            }
            })
          reports = reports.data.filter(report=>report.mahlaka==this.mahlaka)
          
          reports.forEach((report)=>{
              if(reportsSort.hasOwnProperty(report.checkId)){
                 var compareDate =  new Date(report.submitDate)
                  if(compareDate.getTime() > reportsSort[report.checkId].getTime()){
                      reportsSort[report.checkId] =new Date(report.submitDate)
                  }
              }else{
                  reportsSort[report.checkId]  = new Date(report.submitDate);
              }
          })
          this.hamals.forEach((hamal)=>{
            if(reportsSort.hasOwnProperty(hamal.checkId)){
                hamal.lastDateChecked =  moment(new Date(reportsSort[hamal.checkId]),'LLLL','he').format('DD/MM  dddd ')
            }
          })
        

      }else{
          response = await axios.get(this.urlForChecks+`_api/web/lists/getbytitle('checks')/items`,{params:{$filter:`mahlaka eq '${this.mahlaka}'`}});
          this.hamals =  response.data.value;
                reports = await axios.get(this.urlForReports+`_api/web/lists/getbytitle('reports')/items`,{params:{$filter:`mahlaka eq '${this.mahlaka}'`,$select:'submitDate,check,submitter',$orderby:`submitDate desc,check`}})
          reports = reports.data.value
          console.log(reports)
          reports.forEach((report)=>{
              if(reportsSort.hasOwnProperty(report.check)){
                 var compareDate =  new Date(report.submitDate)
                  if(compareDate.getTime() > reportsSort[report.check].getTime()){
                      reportsSort[report.check] =new Date(report.submitDate)
                  }
              }else{
                  reportsSort[report.check]  = new Date(report.submitDate);
              }
          })
          console.log(reportsSort)
          this.hamals.forEach((hamal)=>{
            if(reportsSort.hasOwnProperty(hamal.Title)){
                hamal.lastDateChecked =  moment(new Date(reportsSort[hamal.Title]),'LLLL','he').format('DD/MM  dddd ')
            }
          })
      }

       

          this.isFinished = true;

    }
   },
   components: {
      HamalForm
  },


}
</script>



<style scoped>
.internet{
    font-size:4rem;
        color:rgb(96, 194, 16);
}
