    text-align: left;
}
h3{
    font-size:2rem;
    color:white;
    direction: rtl;
}
.developers-container{
    color:white;
    margin-right: 20px;
    font-size:1.6rem;
    display: flex;
}
.semel-and-title{
    position: absolute;
    bottom:3%;  
    left:50%;
    transform:translateX(-50%);
    display:flex;
    align-items:center;
