    width:10em;
    height:10em;
}
.pre-input-container{
    position: relative;
    height:100vh;
    width:100vw;
    /* padding-top: 100px; */
   
}
.binyan-zahal{
         background-image: linear-gradient(#131313c7, #131313c7),url(@/assets/binyan.jpg);
          background-size: 200%;
        /* animation: setBackground 0.7s cubic-bezier(0.26, -0.03, 0.12, 1.05)  forwards ;  */
                        background-position: 50% 0%;

        background-size: cover;
    animation: fadeIn  0.1s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
}
.report-name-mahlaka-container{
    height: 350px;
    width:600px;
    /* margin-top: 100px; */
    /* background-color: rgba(0, 0, 0, 0.39); */
    border-radius:20px;
    margin-bottom: 5px;
   margin-right:auto;
   margin-left: auto;
    position:relative;
}
.flex-input{
  display:flex;
