  top:0.3vh;
  font-size:6rem !important;
  /* left:50%; */
  margin-bottom:40px;
  text-align: center;
  color:rgb(255, 255, 255);
  font-family:cellFont;

}


input::placeholder{
    color:var(--placeholder-color);
}
input{
 

    direction:rtl;
    padding:10px;
    text-decoration: none;
    outline:none;
    /* top:0.1vh; */
