    border: none;
    /* box-shadow:  var(--shadow-color); */
    border-radius:3px;
}
 

dialog .dialog-action-buttons-container{
    display:flex;
    position:relative;
    flex-direction: column;
    width: 100%;
    font-size:1.5rem;
    top:20px;
    height: 130px;
    justify-content: space-around;
    align-items: center;
    
}
.continue-report-button{
     padding:10px 25px 10px 25px;
    /* transform:translate(-50%,-50%); */
    background-color:var(--primary-clr);
    color:white;
    font-size:25px;
    outline:0 !important;
    border: none;
    /* border:1px solid black; */
