    border:1px solid white;
    padding:15px 15px ;
  animation:appearBlock 0.2s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;                    

}





@keyframes appearBlock {
    0%{
        /* overflow: hidden; */
        opacity:0;
        transform:translateY(60px) scale(0.8) translate(-50%,0) ;
    }
    
  
    100%{
        /* overflow: auto; */
        opacity: 1;
        transform:translateY(0px) scale(1) translate(-50%,0);

    }
    
}
.sub-btn{
    position:absolute;
    top:85%;
    left:50%;
    transform:translate(-50%,-50%);
    /* position */
    /* width:20%; */
    /* height:15%; */
