    /* transform:translate(-50%,-50%); */
       background-color:var(--primary-clr);

    color:white;
    font-size:1.6rem;
    outline:none;
    border:none;
    border-radius: 40px;
        /* box-shadow: 0 0 15px 0 rgba(0,0,0,.2) */
    /* inset: none; */

}
dialog .insert{
    font-weight: bold;
    top:10px;
    font-size:1.5rem !important;
}
dialog form{
    position:relative;
    top:50px ;
}
dialog div{
    position:absolute;
    text-align: center;
}
dialog span{
    display:block;
    position:relative;
    text-align: center;
    margin:0 auto; 
    color:rgb(78, 78, 78);
    top:13px;
        font-size:1.5rem;
    font-weight:normal;
    /* font-style:italic; */
    font-family:cellfont;
    direction:rtl;
}
dialog  h2{
    direction:rtl;
    font-size:3.4rem;
    margin-bottom:0;
    font-weight: bold;
    text-align: center;
    color:rgb(78, 78, 78);
}

.report-name-mahlaka-container  h2{
    direction:rtl;
    font-size:3rem;
    margin-bottom:0;
    font-weight: normal;
    text-align: center;
    color:white;
}
.report-name-mahlaka-container span{
    display:block;
    position:relative;
    text-align: center;
    margin:0 auto; 
    color:white;
    top:13px;
        font-size:1.6rem;
    font-weight:normal;
    font-style:italic;
    font-family:cellfont;
    direction:rtl;
}
.disppear-class{
      animation:disAppear 0.2s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;                    

}
dialog{
  /* left:50%; */
  border:none;
      border-radius: 10px;

  /* top:30%; */
  height:350px;
  width:600px;
  animation:appear 0.2s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;                    
    position: absolute;
  transform:translate(-50%,-50%);
}
@keyframes disAppear{
    0%{
        opacity: 1;
        transform:translateY(0px);
    }
    100%{
        opacity:0;
        transform:translateY(20px);

    }
}
@keyframes appear {
    0%{
        /* overflow: hidden; */
        opacity:0;
        transform:translateY(60px) scale(0.8);
    }
    
  
    100%{
        /* overflow: auto; */
        opacity: 1;
        transform:translateY(0px) scale(1);

    }
    
}

::backdrop{
  background-color:black;
}
dialog::backdrop{
