    <template>
<h3> {{hamal}}</h3>
<div class="posi"> 
  <q-btn-toggle 
        flat
        size="1.5rem"
        v-model="sliders"
        :options="options"
                     />
 <q-carousel  ref="qar" v-if="flag"
        v-model="sliders"
        animated
         transition-prev="slide-right"
         transition-next="slide-left"
      swipeable
        
        class="overflow-class"
         control-color="white"
        color="white"
        height="75%"
      >
         <q-carousel-slide v-for="(table,index) in Object.keys(finalTools)"   :key="index"  :name="table" class="column no-wrap marg "  >
            <TableSlide @on-delay="passDelayCause" @reload-items="reloadItems" @on-comment="passComment" @on-failure="passBoolcheck"  :hamal='hamal' :newTable="finalTools[table]" :type="table" />
          </q-carousel-slide>
        </q-carousel> 
        </div>
        

<!-- 
    <button class="finish-button rounded-pill btn btn-primary" @click="isFinished=true">  </button>

<FinalModal  :checkOutList="checkOutList" :checkDetails="checkDetails" :reportObjectToSend="tableSlide" @onModalClose="func" v-if="isFinished" /> -->

</template>

<script>

import TableSlide from "./TableSlide"
export default {
    props:["tools","hamal","checkDetails","hamalId"],
    name:"ToolsTable",
    data(){
        return{
            flag:false,
            isFinished:false,
            checkOutList:[],
            sliders:" ",
            tableSlide:"",
            options:[],
        }
    },
    
    components:{

        TableSlide
    },
    methods:{
        reloadItems(isFirst){
            console.log('reloaded-carousel')
            this.$emit("reload-items",isFirst)
        },
        passDelayCause(delayCause,title){
                            this.$emit("on-delay",delayCause,title)

        },
        passComment(title,comment){
            this.$emit("on-comm",title,comment)

        },
        passBoolcheck(arr){
            this.$emit("onCheckout",arr,this.hamal)
        }
        ,
  asyncParse(jsonTools){
        return new Promise((resolve,reject)=>{
            resolve(JSON.parse(jsonTools))
        })

    },

        func(){
            this.isFinished  = false;
        },
 
    updateDelayCauses(array,item){
        array = array.forEach((tool)=>{
            if(tool.Title == item.Title ){
                tool.delayCause = item.delayCause
            }
        })

    },
 
          manageArray(){
            this.finalTools = {};
            this.tools.forEach((tool)=>{
                if(!this.finalTools[tool["type"]]){
                    this.finalTools[tool["type"]] = [tool]
                }else{
                this.finalTools[tool["type"]].push(tool)
                }
            
            })

        },
  
    },
    async mounted(){
         this.manageArray()
        console.log("carousel")
    
        Object.keys(this.finalTools).forEach((type)=>{
            this.options.push({label:type,value:type})
        })
   
        this.sliders = this.options[0].value;
   
        this.flag = true;


    },


}
</script>

<style scoped>
.marg{
   
   display: flex;
   align-items: center;
}
.posi{
    position:relative;
    height:var(--posi-height);
    text-align: center;
    color:var(--primary-color);
    /* top:-8%; */
}
.finish-button{
    position:absolute;
    right: 85%;
    font-size: 4vw;
    padding-left:3%;
    padding-right:3%;
    top:80%;    
    /* left:90%;
    font-size:3vw;
    transform: translate(-50%,0); */
}
.carousel-container{
    height:auto;

}
h3{
    direction:rtl;
    text-align: center;
    position: relative;
    color: var(--primary-color);
    /* top:1%; */
    font-size:3rem;
    font-weight: bold;
    /* margin-bottom: ; */

}
.overflow-class{
    overflow-y: auto;
    background-color: transparent;
}
.main-container{
    height:200vh;
    width:100vw;
}

</style>