<template>


  <div class="main-container" ref="background">
    <div class="spinner-container">
        <div class="spinner-border text-light loading-hamal-spinner"  v-if="isSpinner"> </div>
        </div>
    <div class="hamals-container"> 




                    <q-btn-toggle
            flat
            size="1.4vw"
            v-model="sliders"
            :options="options"
                
                /> 
                </div>

    
                <q-carousel  v-if="flag" ref="maincar"
            v-model="sliders"
            animated
            transition-prev="slide-right"
            transition-next="slide-left"
        
            arrows
            class="background-colors"
            control-color="white"
            color="white"
            height="800px"
        >
            <q-carousel-slide v-for="(hamal,index) in hamalsList"   :key="index"  :name="hamal.hamalName" class="column no-wrap  it"  >
                    <Carousel v-if="$mahlaka == 'vc'"  @reload-items="newReloadFunc" @on-delay="updateDelay" @on-comm="updateLocal" @on-checkout="handlerFunc" :hamal="hamal.hamalName" :hamalId="hamal.id" :tools="hamal.tools" :key="tabKey" />
            </q-carousel-slide>

            </q-carousel>
                        <!-- <CheckOut :checkOutList="checkOutList" /> -->
    <div class="finish-link-container"> 
     <router-link v-if="false" class="proceed-link" :to="{path:'check/final',name:'final',params:{checkDetails:JSON.stringify({location:3,userName:userName,mahlaka:mahlaka,date:date,checkName:checkName,checkId:checkId})}}">   <span class="bi bi-arrow-right"> </span> </router-link>
        <router-link  class="to-main-menu-link" to="/">   <span class="bi bi-arrow-left"> </span> </router-link>

</div>
      </div>    






</template>

<script>
import Carousel from "@/components/edit/Carousel.vue"
import moment from "moment"
import axios from "axios"

export default {
    name:"Check",
    props:[],
    data(){
        return{
             date:moment(new Date(),'LLLL','he').format('DD/MM  dddd '),
             pages:"0" ,
             sliders:"0",
            tools:[],
            options:[],
            flag:false,
            checkName:"",
            isFinished:false,
            hamalsList:"",
            checkId:"",
            finalTools:"",
            location:"",  
            isSpinner:true,
            checkOutList:[],
            userName:"",
            mahlaka:"",
  urlForHamalsLocal: `${this.$url}`,
             urlForToolsLocal: `${this.$url}`,
             newvalUrl: `${this.$url}`,
            chosenValue : '', //newval
            tabKey:0,
         }
    },
    beforeMount(){
        window.addEventListener("beforeunload",()=>{

            if (this.checkOutList.length > 0){
                localStorage.setItem("checkout-list",JSON.stringify(this.checkOutList))
            }

            if (this.hamalsList){
                localStorage.setItem("tools",JSON.stringify(this.hamalsList))
            
            }
        },{once:true})
       
    },
    beforeUnmount(){
        this.theBeforeMountedFunction()
        this.isSpinner = false;
              
      
    },
 

   async mounted(){
          this.theMountedFunction()
 
    },
    components:{
        Carousel,


    },
    methods:{
        newReloadFunc(isFirst){
                 this.theMountedFunction(isFirst)       
        },
        async getToolsDjango(){
            console.log("reloaded")
              this.changePic(this.$route.params["location"])
                this.checkId = this.$route.params["checkId"]
         
             var hamalsResponse = await axios.get(this.urlForHamalsLocal+`/hamal/check/${this.checkId}/`,{
                headers:{
                    Authorization:`Token ${sessionStorage.getItem("token")}`
                }
            })

        hamalsResponse = hamalsResponse.data.map((hamal)=>{
            var obj = {}
            obj[hamal["hamalId"]] = hamal["Title"];
          return  obj 
            })
        var promisesArr = [...hamalsResponse]
        promisesArr = await  axios.all(promisesArr.map((promise)=>{
            
            return axios.get(this.urlForToolsLocal+`/tool/hamal/${Object.keys(promise)[0]}/`,{
                headers:{
                Authorization:`Token ${sessionStorage.getItem("token")}`
                }
            })

        }))
        promisesArr = promisesArr.map(res =>res.data)
        promisesArr = promisesArr.map((tool)=>{
            var hamalId = tool[0].hamalId;
            const hamalName  = hamalsResponse.filter(hamal=>Object.keys(hamal)[0] == hamalId)[0][hamalId]
            return {"hamalName":hamalName,tools:[...tool]}
        })
       



           
                this.hamalsList =[...promisesArr] 
                this.changePic(' "')
                
      if(!this.options.length){
                this.hamalsList.forEach((hamal)=>{
                    this.options.push({label:hamal.hamalName,value:hamal.hamalName})
                })
             this.sliders = this.options[0].value;

                }

            this.flag= true;
           this.isSpinner = false;   

        },

 async getToolsSp(){
                     this.changePic(' "')

               this.checkId = this.$route.params["checkId"]
              
             var hamalsResponse = await axios.get(this.newvalUrl+`/_api/web/lists/getbyTitle('rooms')/items`,{params:{$filter:`checkId eq '${this.checkId}'`}}) 

        hamalsResponse = hamalsResponse.data.value.map((hamal)=>{
            var obj = {}
            obj[hamal["hamalId"]] = hamal["Title"];
          return  obj 
            })
        var promisesArr = [...hamalsResponse]
        promisesArr = await  axios.all(promisesArr.map((promise)=>{
            const hamalId =  Object.keys(promise)[0]
            return  axios.get(this.newvalUrl+`/_api/web/lists/getbyTitle('trialList2')/items`,{params:{$filter:`hamalId eq '${hamalId}'`}})

        }))
        promisesArr = promisesArr.map(res =>res.data.value)
        promisesArr = promisesArr.map((tool)=>{

            var hamalId = tool[0].hamalId;
            //adds the django id (with no caps) to the sharepoint 
            tool.forEach((singleTool)=>{
                singleTool.id = singleTool.Id
            })
            const hamalName  = hamalsResponse.filter(hamal=>Object.keys(hamal)[0] == hamalId)[0][hamalId]
            return {"hamalName":hamalName,tools:[...tool]}
        })
       



           
                this.hamalsList =[...promisesArr] 
                //
             
             this.hamalsList = await this.asyncParsedJson()
                   
          
                
        
    if(!this.options.length){
                this.hamalsList.forEach((hamal)=>{
                    this.options.push({label:hamal.hamalName,value:hamal.hamalName})
                })
             this.sliders = this.options[0].value;

                }
   
                                       this.isSpinner = false;   
        this.flag = true;
        },
    asyncParsedJson(){
        return Promise.all(this.hamalsList.map((hamalObject)=>{
                 return Promise.all(hamalObject.tools.map((tool)=>{
                     return new Promise((res,rej)=>{
                         res(JSON.parse(tool.details))
                     }).then((jsonParsed)=>{
                         tool.details = jsonParsed
                         return tool
                     })
                 })).then((toolsArray)=>{
                     hamalObject.tools =[...toolsArray ]
                     return hamalObject
                 })
                
                 }))
    },

    getCheckIdkByCheckLocal(value){
          //newval
            // return axios.get(this.urlForChecksLocal,{params:{"Title":value}})
            return axios.get(this.newvalUrl + '/check/name/' + value +'/')
        },
    getHamalIdByTitleLocal(checkId){
        //newval
            // return axios.get(this.urlForHamalsLocal,{params:{"checkId":checkId}})
            return axios.get(this.newvalUrl + '/hamal/check/' + checkId +'/')
    },
    getToolsByHamalIdLocal(hamalId){
        //newval
                // return  axios.get(this.urlForToolsLocal,{params:{"hamalId":hamalId}})
                return axios.get(this.newvalUrl + '/tool/hamal/' + hamalId +'/')
    },
    asyncStringify(toolsArray){
        return new Promise((resolve,reject)=>{
            resolve(JSON.stringify(toolsArray))
        })

    },
        updateDelay(delayCause,title){

                 this.checkOutList.forEach(item=>{
                        if(item.Title == title){
                            item.delayCause =delayCause
                        }
                    })
                localStorage.setItem("checkout-list",JSON.stringify(this.checkOutList))
                                        localStorage.getItem("checkout-list")

        },
        updateLocal(title,comment){
         
                    this.checkOutList.forEach(item=>{
                        if(item.Title == title){
                            item.comments =comment
                        }
                    })
                    localStorage.setItem("checkout-list",JSON.stringify(this.checkOutList))
                                        localStorage.getItem("checkout-list")


        },
        handlePass(value){
        

            this.$refs["maincar"].goTo(value)
    
                   
        },
     func(){
            this.isFinished  = false;
        },
        asyncParse(jsonTools){
            //empty!!!!!!

            return new Promise((resolve,reject)=>{
                resolve(JSON.parse(jsonTools))
            })

    },   checkIfItemInArray(array,Title){
        var returnedValue = false;
        array.forEach((tool)=>{
            if (tool.Title == Title){
                returnedValue = true
            }
        })
        return returnedValue
    },
     checkIfTakul(object){
            if(Object.keys(object).some((element)=>{
                return !object[element]
            })){
                return true;
              
            }else{
                return false;
            }
        },
        
updateDelayCausesAndBoolChecks(array,item){
        array = array.forEach((tool)=>{
            if(tool.Title == item.Title ){
                tool.delayCause = item.delayCause
            }
            // tool.details.boolChecks ={...item.details.boolChecks}
        })

    },

    
      
        changePic(value){
            // const background =  document.body;
            const background =  this.$refs.background;
            if(value == ' '){
             background.classList.add("mezudat-zion")

        } if(value ==' "'){  
              background.classList.add("binyan-zahal")
        }

            if(value ==''){
              background.classList.add("marganit")

            }

        },
             handlerFunc(arr,hamal){
                var addToCheckOutArr = []
                    var removeFromCheckOutArr = []
                    var isIncluded;
                    var isTakul;
        
                    arr.forEach((item)=>{
                        item["hamal"] = hamal;
                        isIncluded = this.checkIfItemInArray(this.checkOutList,item.Title)
                        isTakul = this.checkIfTakul(item.details.boolChecks)
            
                        if(!isIncluded && isTakul){
                            addToCheckOutArr.push(item)
                        }
                        if(isIncluded && isTakul){
                            this.updateDelayCausesAndBoolChecks(this.checkOutList,item)
                                this.checkOutList.forEach(tool=>{
                                if(tool.Title == item.Title){
                                    tool.details.boolChecks = {...item.details.boolChecks}
                            }
                    })

                        }
                        if(isIncluded && !isTakul){
                            removeFromCheckOutArr.push(item)

                        }
                    
                    })
                    removeFromCheckOutArr.forEach((item)=>{
                        this.checkOutList = this.checkOutList.filter((del)=>{
                            return del.Title !== item.Title;
                        })
                    })
                
                    
                    this.checkOutList =[...this.checkOutList,...addToCheckOutArr]
                      

                    localStorage.setItem("checkout-list",JSON.stringify(this.checkOutList))
                    //  localStorage.setItem("checkout-list",JSON.stringify(this.checkOutList))
                },
    async theMountedFunction(isFirst){
        this.changePic(' "')
                    if(isFirst){
                     this.flag =false;

                    }

         if(this.$isDjango){
              
                
                // this.hamalsList =this.$route.params["hamalsList"]
    
                // this.hamalsList =await this.asyncParse(this.hamalsList) 
                   this.getToolsDjango()
              
            
            }else{
                   this.getToolsSp()      
            }


 

        
    },
    theBeforeMountedFunction(){
      localStorage.clear()

        this.flag = false;
    }

    }
}
</script>

<style>
