<script>
import CheckOut from "@/components/check/CheckOut.vue"
import axios from "axios"
import moment from "moment"
export default {
    name:"BeforeSend",
    data(){
        return{
                      isOnline:navigator.onLine,

            finalDate: moment(new Date(),'LLLL','he').format('DD/MM  dddd '),
            requestFinalSend:{}
            , finalMails:['i:0#.w|army\\r03830010'],
            comment:"",
            url:"https://portal.army.idf/sites/gdud0383/team/_api/SP.Utilities.utility.SendEmail",
             urlFormDigest:"https://portal.army.idf/sites/gdud0383/Team/_api/contextinfo",
            requestFinalUrl:`${this.$url}`,
            // mailList:[{listName:" 383",isSent:false,nimanim:' , ',mail:["s8422760@army.idf.il","s9007598@army.idf.il"]}],
            mailList:[{listName:" 383",isSent:false,nimanim:' ',mail:["s8739496@army.idf.il","s7392542@army.idf.il"]},
            {listName:" 383",isSent:false,nimanim:' , ',mail:["s8422760@army.idf.il","s9007598@army.idf.il"]}
                        ]
                        ,
            tkulim:"",
             Ystyle:{
                "--clip-path":'polygon(14% 44%,0 65%,50% 100%,100% 16%, 80% 0, 43% 62%)',
                "--shadow-color":'rgb(104, 243, 76)'
            },
            checkDetails:"",
        }
    },
    components:{
        CheckOut
    },
  mounted(){
      this.addConnectionsEventListeners()
        this.changePic(this.checkDetails["location"])
        this.handleAnimations()
    },
    beforeMount(){

            if (localStorage.getItem("checkout-list")){
                    this.tkulim = JSON.parse(localStorage.getItem("checkout-list"))
                    this.checkDetails = JSON.parse(localStorage.getItem("route-params"))
                                //  console.log(JSON.parse(localStorage.getItem("checkout-list")))

        }else{
                                this.tkulim = JSON.parse(this.$route.params.tkulim)
                      this.checkDetails = JSON.parse(this.$route.params.checkDetails)

        }
                       console.log(this.checkDetails)

    },

    methods:{
            showStatus(){
            if(navigator.onLine){
                this.isOnline = true;
                    this.$q.notify({message:" ",type:"positive"})

            }else{
                                this.isOnline = false;

                this.$q.notify({message:" ",type:"negative"})

            }
       },
       addConnectionsEventListeners(){
            window.ononline = this.showStatus;
            window.onoffline = this.showStatus;

                
            
       },
        handleAnimations(){
            this.$refs["mail-container"].addEventListener("animationend",()=>{
                    this.$refs["appear-container"].classList.add("appear-animation-class")
            },{once:true})
            this.$refs["mail-container"].classList.add("stretch-animation-class")
        },
      
        hashFunc(str){
            var hash = 0 ,i,chr;
            if(str.length ===0){
                return hash
                }
            for(i =0;i<str.length;i++){
                chr= str.charCodeAt(i);
                hash = ((hash << 5) - hash) + chr;
                hash |= 0;
            }
            return Math.abs(hash).toString();
            
        },
        toggleSent(isSent,mails){
          if(!isSent){
              mails.forEach((Ma)=>{
                this.finalMails.push(Ma)

              })
          }  else{

                mails.forEach((Ma)=>{
                        this.finalMails= this.finalMails.filter(mail=>mail!==Ma)

              })

          }
          console.log(this.finalMails)
        },
          changePic(value){
            // const background =  document.body;
            const background =  this.$refs.background;
            if(value == ' '){
             background.classList.add("mezudat-zion")

        } if(value ==' "'){  
              background.classList.add("binyan-zahal")
        }

            if(value ==''){
              background.classList.add("marganit")

            }

        },
     postReportDjango(){
         //can remove X-RequestDigest if no token needed
        var requestObject = {
                  headers :{
                         Authorization:`Token ${sessionStorage.getItem("token")}`,
                    Accept:"application/json;odata=verbose"
                    }
            }
            var requestBody = {
                submitDate:new Date(),
                submitter: this.requestFinalSend["submitter"],
                tkulim:JSON.stringify(this.requestFinalSend["tkulim"]),
                mahlaka:this.requestFinalSend["mahlaka"],
                comment: this.requestFinalSend["comments"],
                checkId:this.requestFinalSend["checkId"],

            }
            return axios.post(this.requestFinalUrl+`/report/create/`,requestBody,requestObject)
        },
            postReportSp(token){
         //can remove X-RequestDigest if no token needed
        var requestObject = {
                  headers :{
                        "X-RequestDigest":token,
                        "content-type":"application/json;odata=verbose",
                        "Accept":"application/json;odata=verbose"
                    }
            }
            var requestBody = {
                __metadata: {type: "SP.Data.ReportsListItem"},
                                Title: "",

                submitDate:new Date(),
                reportId :this.requestFinalSend["reportId"],
                check: this.requestFinalSend["check"] ,
                submitter: this.requestFinalSend["submitter"],
                tkulim:JSON.stringify(this.requestFinalSend["tkulim"]),
                mahlaka:this.requestFinalSend["mahlaka"],
                comment: this.requestFinalSend["comments"],
                // checkId:this.requestFinalSend["checkId"],

            }
            return axios.post(this.requestFinalUrl+`/_api/web/lists/getbytitle('reports')/items`,requestBody,requestObject)
        },


         getToolErrors(obj){
            var returnedString= ""
            Object.keys(obj).forEach((item)=>{
                if(!obj[item]){
                    returnedString += item +" ,";
                }
            })
            returnedString = returnedString.slice(0,returnedString.length-1)
            return returnedString
        },
        sendEmail(token){
     var tableBackgroundColor = '000000';
     var backgroundPic; 
     if (this.requestFinalSend["location"] == ' '){
         backgroundPic = "url('https://portal.army.idf/sites/gdud383/ktechni/SiteAssets/mivzaim.png') "
     }
    if(this.requestFinalSend["location"] == ' "'){
            backgroundPic = "url('https://portal.army.idf/sites/gdud383/ktechni/SiteAssets/zahal.png') "
     
    }
    if(this.requestFinalSend["location"] == ''){
             backgroundPic = "url('https://portal.army.idf/sites/gdud383/ktechni/SiteAssets/marganit.png')  "
    }

	var subject =`         ${this.requestFinalSend["submitter"]} "  ${this.requestFinalSend["check"]}`

    const newBody = `<html xmlns="http://www.w3.org/1999/xhtml"><head>
<meta http-equiv="Content-Type" content="text/html; charset=utf-8">
    <meta name="viewport" content="width=device-width; initial-scale=1.0; maximum-scale=1.0;">
</head>
<body style="margin: 0; padding: 0; width: 100%; height: 100%; background: #F8F8F8;">
<!---->
<table width="90%" border="0" cellpadding="0" cellspacing="0" class="BGtable" style="border-collapse: collapse; margin: 0px; padding: 0px; background-color: #f8f8f8; height: 100%; width: 100%;">
    <tr>
        <td valign="top" class="BGtable-inner" style="padding: 10px;">
            <table width="100" border="0" align="center" cellpadding="0" cellspacing="0" class="row fg rounded-corner" style="border-collapse: collapse; background-color: #ffffff; border-top-left-radius: 6px; border-top-right-radius: 6px; border-bottom-right-radius: 6px; border-bottom-left-radius: 6px; width: 350px;">
                <tr>
                    <td>
                        <table width="100%" border="0" cellspacing="0" cellpadding="0" style="border-collapse: collapse;text-align: center;">
                            <tr>
                                <td>
                                    <a href="https://portal.army.idf/sites/gdud0383/Team/dohamal/index.html#/">
                                        
                                            <img src="https://portal.army.idf/sites/gdud0383/Team/SiteAssets/dohamalTitle.png" alt="image" border="0" class="tP img" style="border:0; width: 400px">
                                        
                                    </a>
                                </td>
                            </tr>
                        </table>
                        <table width="90%" border="0" cellspacing="0" cellpadding="0" style="border-collapse: collapse;" align="right" dir="rtl">
                            <tr>
                                <td class="content normal-td" style="padding-bottom: 0px; padding-left: 0px; padding-right: 0px; padding-top: 0px; font-family: Questrial, 'Helvetica Neue', Helvetica, arial; font-size: 13px; line-height: 20px; font-weight: 400; color: #555555; padding: 20px -1px 0;">
                                    <div style="font-family: Arial,serif; font-size: 12pt;" dir="rtl">
                                     
                                        <p>         ${this.requestFinalSend["submitter"]}  </p>
                                

                                      <p>":  <b>   ${this.requestFinalSend["check"]} </b> </p>
                                      
                                     
                                        
                                        
                                        <a href="${this.emailLink}" style="color: #3f9e09; text-decoration: initial; outline: none;" target="_blank">
                                            <img src="https://portal.army.idf/sites/gdud0383/Team/SiteAssets/lower.jpg" border="0" alt="button" style="border: 0; display: block; float: right;" dir="rtl">
                                        </a>
                                        
                                    </div>
                                </td>
                            </tr>
                        </table>
                    </td>
                </tr>
            </table>
            <table width="600" border="0" align="center" cellpadding="0" cellspacing="0" class="row" style="border-collapse: collapse; width: 600px;">
                <tr>
                    <td height="28">
                        <img src="https://portal.army.idf/sites/gdud0383/Team/SiteAssets/divider.png" width="600" height="28" class="divider" style="border: 0; display: block; -ms-interpolation-mode: bicubic; height: 28px;">
                    </td>
                </tr>
            </table>
        </td>
    </tr>
</table>
</body>
</html>
`
    
	var requestBody  = JSON.stringify({ 
	properties:{
		From:"s8704173@army.idf.il",
		To:{"results":this.finalMails},
		Body:newBody,
		Subject:subject
		
	}
    
	})
    
