            "accept":"application/json;odata=verbose",
            "content-type":"application/json;odata=verbose"
        }
    } 
    var title = " "
    var text = "      "
	return axios.post(this.url,requestBody,requestConfigObject).catch(()=>{this.$swal.fire(title,text,"error")})
	
	
	
  
},

         getDigestValue(){
               var title = " "
                var text = "      "
                return axios.post(this.urlFormDigest).catch(()=>{this.$swal.fire(title,text,"error")})
            },
            async sender(){
                if(!window.navigator.onLine){
                    this.noConnectionSwal(this.$swal)
                     return;
                    }
                    try{
                        const response = await this.sendRandomRequest()
                    }catch{
                        this.noConnectionSwal(this.$swal)
                        return;
                    }
                console.log(this.finalMails)
                var reportId = this.hashFunc(Date.now() + this.checkDetails["mahlaka"])
                console.log(reportId)
                this.requestFinalSend["reportId"] =reportId
                this.requestFinalSend["comments"] = this.comment;
                this.requestFinalSend["submitDate"] =this.checkDetails["date"];
                this.requestFinalSend["checkId"] = this.checkDetails["checkId"]
                this.requestFinalSend["mahlaka"] = this.checkDetails["mahlaka"]; 
                this.requestFinalSend["check"] = this.checkDetails["checkName"];
                this.requestFinalSend["location"] = this.checkDetails["location"];
                this.requestFinalSend["submitter"] =this.checkDetails["userName"],
                this.requestFinalSend["Title"] = " "
                this.requestFinalSend["tkulim"] = {...this.tkulim}
                        


                if(this.$isDjango){
                    var responseReport = await this.postReportDjango()
                     if(responseReport){
                        this.emailLink = `${this.$url}?reportId=${responseReport.data.id}`
                        this.$swal.fire("   ","","success")
                        localStorage.clear();
                        this.$router.push({path:"/",name:"Main"})
                     }
                }else{
                this.emailLink = `${this.$url}/report/index.html?reportId=${this.requestFinalSend["reportId"]}`
                 var tokenResponse =  await this.getDigestValue();
                var token = tokenResponse.data.FormDigestValue;
                
                var emailResponse =  await this.sendEmail(token);
                if (emailResponse){
                   
                     var responseReport = await this.postReportSp(token)
                     if(responseReport);
                     this.$swal.fire("   ","","success")
                     localStorage.clear();
                     this.$router.push({path:"/",name:"Main"})

              }
                }
          


            



            this.$emit("onModalClose")




        },
    },
  
}
</script>

