import { createRouter, createWebHashHistory } from 'vue-router'
import CheckEdit from '../views/CheckEdit.vue'
import MainPage from '../views/MainPage.vue'
import MainCheck from "../views/MainCheck.vue"
import Check from "../views/Check.vue"
import BeforeSend from "../views/BeforeSend.vue"

const routes = [

  {
    path: '/',
    name: 'Main',
    component: MainPage
  },{
    path: '/edit/edit-ui',
    name: 'CheckEdit',
    component: CheckEdit
  },


  {
    path:"/check/:checkId",
    name:"check",
    component:Check
    }, {
    path:"/edit/:checkId",
    name:"edit",
    component:CheckEdit
    },
    {
      path: '/:mode',
      name: 'mode',
      component: MainCheck
    },
  
  {
  path:"/check/final",
  name:"final",
  component:BeforeSend,
  props:(route)=>({
    ...route.params
  }),

  
}
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
