            <div v-for="(boolCheck,index) in Object.keys(tool.details.boolChecks)" :key="index" >
            <input :id='boolCheck'   class="checkbox-bug" type="checkbox" :checked="true" v-model="newToolData.details.boolChecks[boolCheck]" :ref="tool.Title+'boolcheck'+index"  @change="updateBoolChecks(boolCheck)" > 
                        <label :for="boolCheck" >{{boolCheck}}</label>
            </div>
        </div>
                <div class="delay-cause-container" v-if="delayCauseFlag" > 
                <h6>  </h6>
              <select  @input="updateDelayCause(delayCause)"  v-model="delayValue" ref="DelayInput" class="form-select form-select-sm categories-class dropdown-menu-right " size="1" aria-label=".form-select-lg example">
            <option v-for="reason in reasonsArray" :key="reason" :value="reason">{{reason}} </option> 
            

            </select> 
                </div>

                <h4> </h4>
                <h6 v-if="!tool.details.info.length">     </h6>
  <div class="flex-info">
                 <div   v-for="(info,index) in Object.keys(tool.details.info)" :key="index"  class="info-wrapper">
        <q-input   @update:model-value="updateInfo(info)"  v-model="newToolData.details.info[info]" :ref="'info'+info"  type="text" dir="rtl" class="direction-class"  outlined :placeholder="` ${info}`"> </q-input>
            </div>
        </div>
                        <h4> </h4>
                <div class="comments-wrapper"> 
                <q-input aria-rowspan="2" v-model="newToolComments"  type="textarea" dir="rtl" class="textarea"  outlined placeHolder=" " @update:model-value="updateToolData" > </q-input>
                </div>

    <div class="flex-btns"><button class="cancel-btn" @click="handleCloseDialog($refs['add-tool-dialog'])"> </button> <button class="submit-btn" ref="submit-btn" :disabled="isValid" @click="postNewDevice" > </button >   </div>
     </dialog>
