    },
    ChangeToClass(){
        this.$refs['buttonAdd'].classList.value = 'buttonAdd'
        this.$refs['trTop'].classList.value = 'tr-top'
    },
    updateDelayCause(delayValue){
        // console.log(delayValue)
        // console.log(this.$refs['DelayInput'])
        this.delayValue = delayValue
        this.newToolData.delayCause = delayValue
    },
    updateToolData(){
        this.newToolData.Title = this.newToolName
        this.newToolData.ip = this.newToolIp
        this.newToolData.comments = this.newToolComments
        this.checkIfValidInput() 
    },
    updateBoolChecks(boolCheck){
        //toggle boolCheck
   
        //check for display delayCause
        this.delayCauseFlag = this.atListOneBoolCheckFalse()
    },
    updateInfo(info){
      
        // this.newToolData.details.info.forEach(element =>{
        //     if (Object.keys(element)[0] == info){
        //         console.log(this.$refs)
        //         console.log(this.$refs['info'+info][0].value)
        //         element[info] = this.$refs['info'+info][0].value //value of the info input
        //     }
        // })
        // this.checkIfValidInput() 
    },
    atListOneBoolCheckFalse(){
        let flag = false
        Object.keys(this.newToolData.details.boolChecks).forEach(obj =>{
            // console.log( obj[Object.keys(obj)[0]])
            if (!this.newToolData.details.boolChecks[obj]){ //if value of the boolCheck is false
                flag = true
            }
        })
        return flag
    },
    async postNewDevice(){
        // if(this.newToolData.Title != ''){
        
        this.handleCloseDialog(this.$refs['add-tool-dialog'])
        this.$swal({
            title:' ',  
            text:' '
        })
        this.$swal.showLoading()
        let copy = {...this.newToolData}
        if(this.newToolData.Title != '' && this.newToolData.ip != ''){ 

        var item = await DAPI.PostTool(copy)

       
        this.$emit("add-item", item)
    }
    else{ //if not valid input
    
    // this.$q.notify("fill all details")
    }
    },
    // reloadItems(){
    //     this.$emit("reload-items")
    // },
    arrToObj(arr){
        var obj = arr.reduce(function(result, item){
            var key = Object.keys(item)[0];
            result[key] = item[key];
            return result;
        },{})
        return obj
    },
    },
   beforeMount(){
    //sets the boolchecks and info from table[0] 
    const toolBoolChecks = this.$props['tool'].details.boolChecks
    const toolinfo = this.$props['tool'].details.info
    Object.keys(toolBoolChecks).forEach(element => {
        var obj = {}
        obj[element] = true 
        this.newToolData.details.boolChecks.push(
            obj
        )
    });
    Object.keys(toolinfo).forEach(element => {
        var obj = {}
        obj[element] = ''
        this.newToolData.details.info.push(
            obj
        )
    });
    //sets type, hamalId and comments
    this.newToolData.type = this.$props['tool'].type
    this.newToolData.hamalId = this.$props['tool'].hamalId
    this.newToolData.details.info = this.arrToObj(this.newToolData.details.info)
        this.newToolData.details.boolChecks = this.arrToObj(this.newToolData.details.boolChecks)

    // console.log('hamal and type:')
    // console.log(this.$props['type'])
    // console.log(this.$props['hamal'])
    },
    mounted(){ 
    // console.log(this.newToolData)   
    }
}
