    PutTool:async function(tool){
        if (IsDjango){
              if (tool.delayCause != null){
                        tool.delayCause = tool.delayCause.toUpperCase()
                    }
            var data = 
                {
                    "comments": tool.comments,
                  
                    "delayCause": tool.delayCause,
                    "Title": tool.Title,
                    "ip": tool.ip,
                    "boolChecks": tool.details.boolChecks,
                    "info": tool.details.info,
                    "hamalId" : tool.hamalId,
                    "type" : tool.type
                }
            
            let response = axios.put(url+'/tool/update/'+tool.id+'/',data,{
                headers:{
                Authorization:`Token ${sessionStorage.getItem("token")}`
                }
            });
            return response
            }
            else{
                let token = await axios.post(url+'_api/contextinfo')
                token = token.data.FormDigestValue;
                if (tool.delayCause != null){
                    tool.delayCause = tool.delayCause.toUpperCase()
                }
                const requestObject = {
                    headers :{
                        "X-RequestDigest":token,
                        "X-HTTP-METHOD":'MERGE',
                        "IF-MATCH":'*',
                        "content-type":"application/json;odata=verbose",
                        "Accept":"application/json;odata=verbose"
                    }
            }
                tool.details = JSON.stringify(tool.details)
                 const requestBody = {
                __metadata: {type: "SP.Data.TrialList2ListItem"},
                    type: tool.type,
                    details:tool.details,
                    delayCause: tool.delayCause,
                    Title: tool.Title,
                    ip: tool.ip,
                    hamalId : tool.hamalId,
                    comments: tool.comments,

                // checkId:this.requestFinalSend["checkId"],

            }
                   var response = await  axios.post(url+`/_api/web/lists/getbytitle('trialList2')/items(${tool.id})`,requestBody,requestObject)

        
            tool.details = await asyncParse(tool.details)

            tool.id = tool.Id
            return tool
            }
    },
