   <!-- <dialog ref="options-dialog"  v-if=" false"> -->
  
        <h1 class="main-title"> "</h1>
  
    <div class="report-name-mahlaka-container"> 

        <span>{{date}} </span>
        <div class="flex-input">
           <router-link  class="secondary-link" :to="{name:'mode',path:'/edit',params:{mode:'edit'}}">    </router-link>
            <router-link  class="main-link" :to="{name:'mode',path:'/check',params:{mode:'check'}}">   </router-link>
         
            </div>

 
</div>
<div class="semel-and-title"> 
    <div class="developers-container">
            
