<script> 
import axios from "axios"
import moment from "moment"
import DAPI from "../assets/scripts/djangoAPI.js"

export default {
   name: 'Main',

   data(){

      return{
          $q:"",
          isWaitingHamalResponse:false,
         isLocalEmpty:true,
            userName:"",
            isFormDialog:false,
            date:moment(new Date(),'LLLL','he').format('DD/MM  dddd '),
            tashtiotString:'',
            hamalsString:' "',
            url:`${this.$url}`,

            isFinished :false,
            mahlaka:"VC",
            hamal:"",
            hamals:[],

    }  
   },
    async mounted(){
    if(!sessionStorage.getItem('token') && this.$isDjango){
            const loginResponse = await  this.$swal({
            title:'',
            allowOutsideClick:false,
            confirmButtonClass:"confirm-btn-class",
            confirmButtonColor:'#1976D2',
            html:`<input type="text" id="username"  dir='rtl' class="swal2-input" placeholder=" ">
            <input type="password" id="password" dir='rtl'  class="swal2-input" placeholder="">
            `,
            focusConfirm:false,
            preConfirm: async ()=>{
                // console.log(Swal())
                const username =this.$swal.getPopup().querySelector('#username').value
                const password =  this.$swal.getPopup().querySelector('#password').value

                if(!username.trim() || !password.trim()){
                    this.$swal.showValidationMessage(`   `)
                }else{                                 
                 this.$swal.showLoading()
                 let tokenReponse;
                try{
                     tokenReponse = await axios.post(this.url+'/token/',{
                    username:username,
                    password:password
                })
                }catch(msg){
                   
                         if(msg.response.status == 400){
                                     this.$swal.hideLoading()

                        this.$swal.showValidationMessage(`    `)

                }
              
                }
              
        
                sessionStorage.setItem("token",tokenReponse.data.token)
                
                return {username:username,password:password}
                }
            },
            confirmButtonText:''
        })
    }
     

                    


     },
   methods:{
     
      
    


   },
   components: {
  },


}
</script>



