    text-decoration: none;
    outline:none;
    padding:5px 20px;   
    border:1px solid white;
    font-size:1.5rem;
 

}
.loading-hamal-spinner{
    width:10em;
    height:10em;
}
.proceed-link{
    display: inline-block !important;
    position:absolute !important;
