        opacity: 1;
     

    }
    
}

.finish-button{
    position:absolute;
    top:80%;
    border-radius: 50px;
    outline:none;
    border-style: none;
    /* border:1px solid
     white; */
     order:none;

    color:white;
    background-image: linear-gradient( 114deg, rgb(112, 79, 148) 0%, rgb(88, 103, 234) 100%);
    padding:5px 50px;
    font-size:60px;
    transition:background-image 0.2s;
    left:3%;
}
.finish-button:hover{
        background-image: linear-gradient( 114deg, rgb(107, 75, 141) 0%, rgb(61, 74, 189) 100%) !important;

}

.it{
    font-size: 14px;
    color:white;
}
.colors-class{
    background-color: rgb(255, 255, 255,0);
    /* color:black; */
}
.binyan-zahal{
        background-image: linear-gradient(#131313c7, #131313c7),url(@/assets/binyan.jpg);
        background-size: 200%;
        background-position: 50% 0%;
        background-size: cover;

}
.mezudat-zion{
        background-image: linear-gradient(#131313c7, #131313c7),url(@/assets/bor.jpg);
        background-position: 70% 50%;
        background-size: cover;
}
.marganit{
        background-image: linear-gradient(#131313c7, #131313c7),url(@/assets/marganit.jpg);
        background-size: cover;
        background-position: 10% 35%;


}
.sizing{
    font-size:1.2vw;
}
.main-container{

    position: relative;
    height:100vh;
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    width:100vw;
    color:var(--primary-color) !important;
}
.finish-report-link:hover{
    color:white;
}
.finish-report-link{
    display: inline-block !important;
    position:relative !important;
    margin:0 auto;
    /* margin-left:0 auto; */
    text-decoration: none;
    /* width:150px; */
    outline:none;
    color:white;
    /* left:50%; */
    /* transform:translate(-50%,-50%); */
    border-bottom:1px solid white;
    font-size:60px;
    /* padding: 10px 35px; */
    /* border-radius: 40px; */
    /* background-color:rgb(0, 132, 255); */
    
}
.finish-link-container{
    padding-right:50px;
    text-align: right;   
}

</style>